#notfound{
    position:relative;
    height:100vh;
}
#notfound .notfound{
    position:absolute;
    left:50%;
    top:50%;
    -webkit-transform:translate(-50%,-50%);
    -ms-transform:translate(-50%,-50%);
    transform:translate(-50%,-50%);
}
.notfound{
    max-width:920px;
    width:100%;
    line-height:1.4;
    text-align:center;
    padding-left:15px;
    padding-right:15px;

}
.notfound .notfound-404{
    position:absolute;
    height:100px;
    left:50%;
    top:0;
    -webkit-transform:translateX(-50%);
    -ms-transform:translateX(-50%);
    transform:translate(-50%,-50%);
    Z-index:-1;
}
.notfound .notfound-404 h1{
    /* font-family: */
    color: #ececec;
    font-weight:900;
    font-size:276px;
    margin:0px;
    position:absolute;
    left:50%;
    top:50%;
    -webkit-transform:translate(-50%,-50%);
    -ms-transform:translate(-50%,-50%);
    transform:translate(-50%,-50%);

}
 .notfound  h2{
    /* font-family: */
    color: #000;
    font-weight:900;
    font-size:46px;
    text-transform:uppercase;
    margin:0px;}
 .notfound  p{
    /* font-family: */
    color: #000;
    font-weight:400;
    font-size:16px;
    text-transform:uppercase;
    margin-top:15px;}
 .notfound  a{
    /* font-family: */
    color: #000;
   text-decoration:none;
    font-size:14px;
    text-transform:uppercase;
    background: #189cf0;
    display:inline-block;
    padding: 16px 38px;
    border:2px solid transparent;
    border-radius:40px;
    color:#fff;
    font-weight:400;
    -webkit-transition:0.2s all;
    transition: 0.2s all;
    }
    .notfound  a:hover{
        background-color:#fff;
        border-color:#189cf0;
        color:#189cf0;
    }

    @media only screen and (max-width:480px){
        .notfound .notfound-404 h1{
            font-size:162px;
        }
    }
    